<template>
  <div class="detail">
    <!--{{bid}}-->
    <MHeader :back="true">详情页</MHeader>
    <ul>
      <li>
        <label for="bookName">书的名称</label>
        <input type="text" v-model="book.bookName" id="bookName">
      </li>
      <li>
        <label for="bookInfo">书的信息</label>
        <input type="text" v-model="book.bookInfo" id="bookInfo">
      </li>
      <li>
        <label for="bookPrice">书的价格</label>
        <input type="text" v-model.number="book.bookPrice" id="bookPrice">
      </li>
      <li>
        <button @click="update">确认修改</button>
      </li>
    </ul>
  </div>
</template>

<script>
  import MHeader from '../base/MHeader';
  import {findOneBook,updateBook} from "../api";

  export default {
    data() {//组件中的data是函数，返回一个对象
      return {
        book: {}
      }
    },
    watch:{
      $route(){//只要路径变化，就要重新获取数据
        this.getData();
      }
    },
    created() {//页面一加载，需要根据id发送请求
      this.getData();
    },
    methods: {
      async getData() {//通过id找到某一项后，赋给book
        this.book = await findOneBook(this.bid);
        //如果是空对象 需要跳转回列表页
        Object.keys(this.book).length>0?void 0:this.$router.push('/list')
        // console.log(this.book);
      },
      async update(){//点击修改图书信息
        await updateBook(this.bid,this.book)
        this.$router.push('/list');//修改成功后跳转页面
      }
    },
    computed: {
      bid() {
        return this.$route.params.bid;//将路径参数的id映射到bid上

      }
    },
    components: {
      MHeader
    }
  }
</script>
<style scoped lang="less">
  .detail { /*用来遮住详情页的tab*/
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #fff;
    z-index: 20;
  }

  ul {
    margin:50px 20px 0 30px;
    li{
      label{
        display: block;
        font-size: 25px;
      }
      input{
        margin: 10px 0;
        height: 25px;
        width: 100%;

      }
      button{
        width: 60px;
        height: 40px;
        margin-top: 10px;
        background: blue;
        color: #fff;
        border: none;
        text-align: center;
        line-height: 40px;
        outline: none;
        border-radius: 5px;
      }

    }
  }
</style>

<!--
/book?id=3
{bookName}
-->



